<template>
    <div>
      <common-header :title="titlename"></common-header>
      <div class="content"> 
           <div class="topinfo" v-if="this.resumedata">
              <dl>
                  <dt v-if="this.doMainUlr&&this.resumedata.photo_attach.work_photo_path.Text
                  &&this.resumedata.photo_attach.work_photo_path.Text!=='null'"><img :src="this.doMainUlr+this.resumedata.photo_attach.work_photo_path.Text" /></dt>
                  <dt v-else><img src="../assets/images/defaultPhoto.png"/></dt>
                  <dd class="urseName">
                    <span v-if="this.resumedata.person_info">{{this.resumedata.person_info.name.Text}}</span>
                  </dd>
                  <dd >
                    <span v-if="this.resumedata.person_info"><img v-show="this.resumedata.person_info.gender.Text==='女'" src="../assets/images/male.png" width="20" /><img v-show="this.resumedata.person_info.gender.Text==='男'" src="../assets/images/male.png" width="20" />{{this.resumedata.person_info.gender.Text}}</span>
                    <span v-if="this.resumedata.education_experience[0]"><img src="../assets/images/edu.png" width="20"/>{{this.resumedata.education_experience[0].education_degree.Text}}</span>
                    <span v-if="this.resumedata.education_experience[0]"><img src="../assets/images/school.png"  width="20"/>{{this.resumedata.education_experience[0].edu_type.Text}}</span></dd>
                  <dd>
                    <span v-if="this.resumedata.education_experience[0]">{{this.resumedata.education_experience[0].school_name.Text}}</span>
                    <span v-show="this.resumedata.education_experience[0].school_name.Text&&this.resumedata.education_experience[0].major_name.Text">|</span>
                    <span v-if="this.resumedata.education_experience[0]">{{this.resumedata.education_experience[0].major_name.Text}}</span>
                  </dd>
              </dl>
          </div>
          <div class="comment_info clearfix">
              <div>总体评价（必填）：</div>
              <div class="pos_textarea_size">{{getCharLength(this.commentcontent,this.commentmaxlength)}}/{{this.commentmaxlength}}</div>
              <div class="pos_textarea" v-if="this.commentmaxlength">
                <textarea placeholder="请输入" :value="this.commentcontent"  debounce="500" @keydown="textAreaKeyDown($event,commentmaxlength)"  @input="changeInput($event)"></textarea>
              </div>
          </div>
          <div class="comment_result">面试结果：
              <span v-if="this.currentstate!==1">
                  <span :class="cone" @click="setClickState(1)">通过</span>
                  <span :class="ctwo" @click="setClickState(2)">不通过</span>
                  <!-- <span :class="cthree" @click="setClickState($event,3)">待定</span> -->
              </span>
              <span v-else><span class="tspan">通过</span></span>
          </div>
          <div class="comment_list">
            <div class="comment_list_top" @click="showcommentlist($event)">已评评价 <img src="../assets/images/arrow_up.png" v-show="commentliststate"/><img src="../assets/images/arrow_down.png" v-show="!commentliststate"/></div>
            <div class="comment_list_content" v-if="this.commentList&&this.commentList.result&&this.commentList.result.commentList" :style="{height:rowheight}">
                <div class="rowdiv" v-bind:key="index" v-for="(item,index) in this.commentList.result.commentList">
                        <div>{{item.Comment}}</div>
                        <div class="rightdiv"><span v-if="item.DateCreated">— {{item.DateCreated}}</span> <span>{{item.LoginName}}</span></div>
                </div>
            </div>
          </div>
      </div>
      <footer class="btn_footer"  >
       <ul>
         <li @click="saveComment($event)">提交</li>
       </ul>
      </footer>
    </div>
</template>
<script>
import Header from './common/Header.vue';
import Tool from '../configs/tool.js';
import axios from 'axios';
import {mapGetters, mapActions} from 'vuex';
import { doMain,api } from '../../static/dicConfig';

export default {
  name:'ResumeComment',
  components:{
     'common-header': Header
  },
   methods: {
       ...mapActions([
        'getCommentDataList'
      ]),
       getCharLength(value,maxlength){
          return  Tool.getCharLength(value,maxlength);
       },
       saveComment(e){
            //保存评语 与 状态
            if(this.commentcontent===''||this.commentcontent===null){
                mui.toast('请输入总体评价');
                return;
            }
            let commitparams={}
            if(this.currentstate!==this.states&&this.states===1){
                commitparams={
                  CampusId:this.$route.query.campusId,
                  RoomId: this.$route.query.roomId,
                  PostId:this.$route.query.postId,
                  Option:this.states,
                  Comment:this.commentcontent
                }
            }else if(this.currentstate!==this.states&&this.states===2){
                commitparams={
                  CampusId:this.$route.query.campusId,
                  RoomId: this.$route.query.roomId,
                  PostId:this.$route.query.postId,
                  Option:this.states,
                  Comment:this.commentcontent
                }
            }else{
                commitparams={
                  CampusId:this.$route.query.campusId,
                  RoomId: this.$route.query.roomId,
                  PostId:this.$route.query.postId,
                  Option:this.states,
                  Comment:this.commentcontent
                }
            }
            axios.get(`${api.host}/ResumeOption/submitComment`,
            {
            params: commitparams
            }).then((res) => {
                if (res.status === 200) {
                    if (res.data.code === 0) {
                        if(res.data.result){
                            mui.toast('操作成功');
                        }
                    }else{
                        if(res.data.msg){
                            mui.toast(res.data.msg);
                        }
                    }
                }
            })
       },
        textAreaKeyDown(ev,maxlength){
            const value = ev.target.value;
            if(value.length >= maxlength) {
                // 删除:46 退格:8 回车:13
                if (!(ev.which == '46' || ev.which == '8'||ev.which=='37'||ev.which=='38'||ev.which=='39'||ev.which=='40')) {
                    ev.preventDefault();
                }
            }
        },
        changeInput(ev){
            let value = ev.target.value.substr(0,this.commentmaxlength);
            if(ev.target.value.length>999){
                this.commentcontent='';
                this.commentcontent=value;
            }else{
                this.commentcontent=value;
            }
        },
        showcommentlist($event){
            this.commentliststate?this.commentliststate=false:this.commentliststate=true;
            let clen=document.getElementsByClassName("rowdiv").length;
            if(clen>0){
                if(this.commentliststate){
                    let cheight=document.getElementsByClassName("rowdiv")[0].offsetHeight;
                    this.rowheight=((cheight+1)*clen)+'px';
                }else{
                    this.rowheight='0rem';
                }
            }
        },
        setClickState(setstate){
            //-1 删除 0 未操作 2未通过 3待定[定制] 1通过
            switch (setstate) {
                case 1:
                    if(this.states===1)
                    {
                        this.states=0;
                        this.cone='bspan';
                        this.ctwo='bspan';
                        this.cthree='bspan';    
                    }else{
                        this.states=1;
                        this.cone='bspan bspanclick';
                        this.ctwo='bspan';
                        this.cthree='bspan';
                    }
                    break;
                case 2:
                    if(this.states===2)
                    {
                        this.states=0;
                        this.cone='bspan';
                        this.ctwo='bspan';
                        this.cthree='bspan';    
                    }else{
                        this.states=2;
                        this.cone='bspan';
                        this.ctwo='bspan bspanclick';
                        this.cthree='bspan ';
                    }
                break;
                // case 3:
                //     if(this.states===3)
                //     {
                //         this.states='';
                //         this.cone='bspan';
                //         this.ctwo='bspan';
                //         this.cthree='bspan';    
                //     }else{
                //         this.states=3;
                //         this.cone='bspan';
                //         this.ctwo='bspan ';
                //         this.cthree='bspan bspanclick';
                //     }
                //break;
            }
        }
     },
  data () {
    return {
      titlename: '添加评语',
      commentmaxlength:1000,
      commentliststate:false,
      currentstate:0,
      commentlistdata:[],
      resumedata:'',
      states:0,
      commentcontent:'',
      rowheight:0,
      cone:'bspan',
      ctwo:'bspan',
      cthree:'bspan',
      doMainUlr:''
    }
  },
  computed: {
    ...mapGetters({
      commentList: 'commentList'
    })
  },
    created(){
    this.doMainUlr=doMain.url;
    if(localStorage.resumeinfo)
    {
        this.resumedata=JSON.parse(localStorage.resumeinfo);
    }
    if(!!this.$route.query&&!!this.$route.query.state)
    {
        this.setClickState(this.$route.query.state*1);
        this.currentstate=this.$route.query.state*1;
        this.getCommentDataList({campusId:this.$route.query.campusId,roomId: this.$route.query.roomId, postId:this.$route.query.postId});
    }
  },
     
}
</script>

<style lang="less" scoped>
 @import "../assets/css/resumeComment.less";
</style>


